import React, { memo } from 'react';
import { Transition, animated } from 'react-spring';

const AnimatedTextContainer = ({ children, condition }) => (
  <Transition
    native
    items={condition}
    from={{ o: 0, y: 2 }}
    enter={{ o: 1, y: 0 }}
    leave={{ o: 0, y: -2 }}
  >
    {show =>
      show &&
      (({ o, y }) => (
        <animated.div
          style={{
            opacity: o.interpolate(o => o),
            transform: y.interpolate(y => `translateY(${y}rem)`),
          }}
          children={children}
        />
      ))
    }
  </Transition>
);

export default memo(AnimatedTextContainer);
