import React, { memo } from 'react';
import gql from 'graphql-tag';
import styled from 'styled-components';
import { Form } from 'formik';
import { Mutation } from 'react-apollo';
import Sidebar from '../shared/Sidebar';
import Create from './Create';
import Location from './Location';
import Personal from './Personal';
import Success from './Success';
import MultiPageForm, { MultiPageFormContext } from '../shared/MultiPageForm';
import {
  createValidation,
  personalValidation,
  locationValidation,
} from '../../utils/validationSchema';
import { viewports } from '../../utils/variables';

const Register = ({ toggleSidebar, visible }) => {
  return (
    <Sidebar visible={visible} exitSidebar={() => toggleSidebar(undefined)}>
      <Mutation mutation={REGISTER}>
        {(register, { error }) => (
          <MultiPageForm
            screens={[
              <Create
                toggleSidebar={toggleSidebar}
                validate={createValidation}
              />,
              <Personal validate={personalValidation} />,
              <Location validate={locationValidation} error={error} />,
              <Success />,
            ]}
            initialValues={{
              email: '',
              password: '',
              confirmPassword: '',
              firstName: '',
              lastName: '',
              birthdateMonth: null,
              birthdateDay: null,
              birthdateYear: null,
              graduationYear: null,
              major: null,
              streetAddress1: '',
              streetAddress2: '',
              city: '',
              state: '',
              postalCode: '',
            }}
            submit={async values => {
              const { data } = await register({ variables: values });
              return data;
            }}
          >
            <MultiPageFormContext.Consumer>
              {({ activeScreen }) => <Base>{activeScreen}</Base>}
            </MultiPageFormContext.Consumer>
          </MultiPageForm>
        )}
      </Mutation>
    </Sidebar>
  );
};

export default memo(Register);

const Base = styled(Form)`
  padding: 3.4375rem 1.875rem;

  @media ${viewports.medium} {
    padding: 3.4375rem 3.75rem;
  }
`;

const REGISTER = gql`
  mutation register(
    $email: String!
    $password: String!
    $firstName: String!
    $lastName: String!
    $birthdateMonth: SelectInput!
    $birthdateDay: SelectInput!
    $birthdateYear: SelectInput!
    $graduationYear: SelectInput!
    $major: SelectInput!
    $streetAddress1: String!
    $streetAddress2: String
    $city: String!
    $state: String!
    $postalCode: String!
  ) {
    register(
      email: $email
      password: $password
      firstName: $firstName
      lastName: $lastName
      birthdateMonth: $birthdateMonth
      birthdateDay: $birthdateDay
      birthdateYear: $birthdateYear
      graduationYear: $graduationYear
      major: $major
      streetAddress1: $streetAddress1
      streetAddress2: $streetAddress2
      city: $city
      state: $state
      postalCode: $postalCode
    ) {
      user {
        id
        email
      }
    }
  }
`;
