export const getBirthdateDays = month => {
  let numDays = 31;
  if (month) {
    const numDaysValue = {
      january: 31,
      february: 29,
      march: 31,
      april: 30,
      may: 31,
      june: 30,
      july: 31,
      august: 31,
      september: 30,
      october: 31,
      november: 30,
      december: 31,
    };
    const { value } = month;
    numDays = numDaysValue[value.toLowerCase()];
  }
  return Array.from(Array(numDays), (_, x) => ({
    value: (x + 1).toString(),
    label: (x + 1).toString(),
  }));
};

const currentYear = new Date().getFullYear();

export const birthdateYears = Array.from(Array(currentYear - 1899), (_, x) => ({
  value: (x + 1900).toString(),
  label: (x + 1900).toString(),
})).reverse();

export const graduationYears = Array.from(
  Array(currentYear - 1929),
  (_, x) => ({
    value: (x + 1930).toString(),
    label: (x + 1930).toString(),
  })
).reverse();

