import React from 'react';
import { animated, Transition } from 'react-spring';

const HorizontalSlider = ({ visible, start, children, className }) => (
  <Transition
    native
    items={visible}
    from={{ x: start }}
    enter={{ x: 0 }}
    leave={{ x: start }}
  >
    {show =>
      show &&
      (({ x }) => (
        <animated.div
          className={className}
          style={{
            transform: x.interpolate(x => `translateX(${x}%)`),
          }}
        >
          {children}
        </animated.div>
      ))
    }
  </Transition>
);

export default HorizontalSlider;
