import React, { memo } from 'react';
import styled from 'styled-components';
import { Field } from 'formik';
import TextInput from '../inputs/TextInput';
import PasswordInput from '../inputs/PasswordInput';
import Pagination from '../shared/Pagination';
import { fontFamilies, colors, viewports } from '../../utils/variables';
import Wrapper from './Wrapper';

const Create = ({ toggleSidebar }) => (
  <Wrapper
    heading="Create your account"
    subheading="Enter your account details."
  >
    <Field name="email">
      {({ field, form }) => (
        <TextInput
          {...field}
          label="Email Address"
          placeholder="Enter email address"
          error={form.errors.email}
          touched={form.touched.email}
        />
      )}
    </Field>
    <Field name="password">
      {({ field, form }) => (
        <PasswordInput
          {...field}
          label="Password"
          placeholder="Enter password"
          error={form.errors.password}
          touched={form.touched.password}
        />
      )}
    </Field>
    <Field name="confirmPassword">
      {({ field, form }) => (
        <PasswordInput
          {...field}
          label="Confirm password"
          placeholder="Enter password again"
          error={form.errors.confirmPassword}
          touched={form.touched.confirmPassword}
          height={36}
        />
      )}
    </Field>
    <Pagination nextActive />
    <SignIn>
      Already have an account?{' '}
      <SignInLink onClick={() => toggleSidebar('sign-in')}>Sign In</SignInLink>
    </SignIn>
  </Wrapper>
);

export default memo(Create);

const SignIn = styled.div`
  color: #999;
  font-size: 0.875rem;
  margin-top: 3.75rem;
  text-align: center;
`;

const SignInLink = styled.a`
  color: ${colors.blue};
  cursor: pointer;
  font-family: ${fontFamilies.bold};

  @media ${viewports.medium} {
    &:hover {
      color: ${colors.hoverBlue};
    }
  }
`;
