import React, { memo } from 'react';
import styled from 'styled-components';
import { Transition, Spring, animated } from 'react-spring';
import { viewports, colors } from '../../utils/variables';
import { MapBackground } from '../styled/Background';
import { LandingGridCream, LandingGridWhite } from '../styled/Grid';
import { backgroundColor } from './landingData';

const AnimatedBackground = ({ activeScene, visible }) => {
  return (
    <Base visible={visible}>
      <Spring
        native
        from={{
          background: `linear-gradient(to bottom, ${colors.offWhite}, ${
            colors.offWhite
          })`,
        }}
        to={{
          background: backgroundColor(activeScene),
        }}
        config={{ duration: 2000 }}
      >
        {styles => (
          <animated.div
            style={{
              ...styles,
              bottom: 0,
              left: 0,
              position: 'absolute',
              right: 0,
              top: 0,
            }}
          />
        )}
      </Spring>
      <Transition
        native
        items={activeScene >= 0 && activeScene < 2}
        from={{ o: 0 }}
        enter={{ o: 1 }}
        leave={{ o: 0 }}
        config={{ duration: 2000 }}
      >
        {toggle =>
          toggle
            ? ({ o }) => (
                <animated.div style={{ opacity: o.interpolate(o => o) }}>
                  <LandingGridCream />
                </animated.div>
              )
            : ({ o }) => (
                <animated.div style={{ opacity: o.interpolate(o => o) }}>
                  <LandingGridWhite />
                </animated.div>
              )
        }
      </Transition>
      <Transition
        native
        items={activeScene >= 0 && activeScene < 2}
        from={{ o: 0 }}
        enter={{ o: 1 }}
        leave={{ o: 0 }}
        config={{ duration: 2000 }}
      >
        {show =>
          show &&
          (({ o }) => (
            <animated.div style={{ opacity: o.interpolate(o => o) }}>
              <MapBackground />
            </animated.div>
          ))
        }
      </Transition>
    </Base>
  );
};

export default memo(AnimatedBackground);

const Base = styled.div`
  visibility: ${props => (props.visible ? 'visible' : 'hidden')};
  transition: visibility 1ms linear ${props => !props.visible && '1000ms'};

  @media ${viewports.medium} {
    visibility: visible;
  }
`;
