import React, { memo } from 'react';
import styled from 'styled-components';
import { useMedia } from 'the-platform';
import { Transition, animated } from 'react-spring';
import AnimatedText from './AnimatedText';
import { fontFamilies, viewports } from '../../utils/variables';
import Button from '../styled/Button';

const Content = ({ activeScene, toggleSidebar, visible }) => {
  const medium = useMedia(viewports.medium);

  return (
    <Base visible={visible}>
      <AnimatedText activeScene={activeScene} />
      <SubtitleContainer>
        <Transition
          native
          items
          from={{ o: 0, y: 2 }}
          enter={{ o: 1, y: 0 }}
          leave={{ o: 0, y: -2 }}
          config={{ delay: 900 }}
        >
          {show =>
            show &&
            (({ o, y }) => (
              <animated.div
                style={{
                  opacity: o.interpolate(o => o),
                  transform: y.interpolate(y => `translateY(${y}rem)`),
                }}
              >
                <Subtitle>
                  Network with ArtCenter alumni across the globe
                </Subtitle>
              </animated.div>
            ))
          }
        </Transition>
      </SubtitleContainer>
      {!medium && (
        <Transition
          native
          items
          from={{ o: 0 }}
          enter={{ o: 1 }}
          leave={{ o: 0 }}
          config={{ delay: 1200 }}
        >
          {show =>
            show &&
            (({ o }) => (
              <animated.div
                style={{
                  opacity: o.interpolate(o => o),
                }}
              >
                <RegisterButton onClick={() => toggleSidebar('register')}>
                  Register
                </RegisterButton>
              </animated.div>
            ))
          }
        </Transition>
      )}
    </Base>
  );
};

export default memo(Content);

const Base = styled.div`
  color: #111;
  left: 1.25rem;
  position: absolute;
  transform: translate3d(0, -50%, 0);
  top: 50%;
  transition: visibility 1ms linear ${props => !props.visible && '1000ms'};
  visibility: ${props => (props.visible ? 'visible' : 'hidden')};
  width: 90%;
  z-index: 10;

  @media ${viewports.medium} {
    left: 5rem;
    visibility: visible;
    width: 100%;
  }
`;

const SubtitleContainer = styled.div`
  height: 4rem;
  left: 0;
  margin-top: 1rem;
  overflow: hidden;
`;

const Subtitle = styled.h2`
  font-size: 0.925rem;
  font-family: ${fontFamilies.medium};
  letter-spacing: -0.3px;
  line-height: 125%;

  @media ${viewports.medium} {
    font-size: 1.125rem;
  }
`;

const RegisterButton = styled(Button)`
  margin-left: 0;
  margin-top: 2rem;
`;
