import React, { memo } from 'react';
import styled from 'styled-components';
import Select from 'react-select';
import { useMedia } from 'the-platform';
import { Label } from '../styled/Typography';
import { fontFamilies, colors } from '../../utils/variables';
import Toggle from '../shared/Toggle';
import DropdownIndicator from './DropdownIndicator';
import ErrorMessage from './ErrorMessage';
import MultiValueRemove from './MultiValueRemove';
import { viewports } from '../../utils/variables';

const SelectInput = ({
  label,
  placeholder,
  width,
  toggle,
  error,
  options,
  value,
  name,
  form,
  touched,
  className,
  toggled,
  multi,
  handleChange,
}) => {
  const medium = useMedia(viewports.medium);

  const customStyles = {
    valueContainer: base => ({
      ...base,
      color: '#777',
      padding: 0,
    }),
    container: () => ({
      width: width || '100%',
    }),
    control: () => ({
      alignItems: 'center',
      background: '#f8f8f8',
      border: `1px ${error ? colors.red : '#f8f8f8'} solid`,
      borderRadius: '1.25rem',
      color: '#777',
      display: 'flex',
      fontFamily: fontFamilies.regular,
      fontSize: medium ? '0.875rem' : '1rem',
      minHeight: '2.5rem',
      justifyContent: 'center',
      outline: 'none',
      padding: multi
        ? '0.25rem 1rem 0.25rem 0.5rem'
        : '0.25rem 1.25rem 0.25rem 1rem',
      position: 'relative',
    }),
    placeholder: base => ({
      ...base,
      color: '#ccc',
      fontFamily: fontFamilies.regular,
      fontSize: medium ? '0.875rem' : '1rem',
      left: 0,
      marginLeft: 0,
      marginRight: 0,
      opacity: 1,
    }),
    indicatorSeparator: () => ({ opacity: 0 }),
    menu: base => ({
      ...base,
      borderRadius: 0,
      border: '2px #D7D7D7 solid',
      boxShadow: '0 4px 10px rgba(0, 0, 0, 0.06)',
      width: '100%',
    }),
    menuList: base => ({
      ...base,
      background: '#fff',
      padding: 0,
      width: '100%',
    }),
    option: (base, state) => ({
      ...base,
      alignItems: 'center',
      background: state.isFocused ? '#338FFF' : '#fff',
      color: state.isFocused ? '#fff' : '#999999',
      display: 'flex',
      fontFamily: fontFamilies.regular,
      fontSize: medium ? '0.875rem' : '1rem',
      height: '2.5rem',
      lineHeight: '133%',
      paddingLeft: '0.9375rem',
      margin: 0,
    }),
    singleValue: base => ({
      ...base,
      color: '#777777',
      fontFamily: fontFamilies.regular,
      fontSize: medium ? '0.875rem' : '1rem',
      left: 0,
      marginLeft: 0,
      marginRight: 0,
      opacity: 1,
      overflow: 'visible',
    }),
    noOptionsMessage: base => ({
      ...base,
      alignItems: 'center',
      background: '#fff',
      color: '#999999',
      display: 'flex',
      fontFamily: fontFamilies.regular,
      fontSize: medium ? '0.875rem' : '1rem',
      height: '2.5rem',
      margin: 0,
      paddingLeft: '0.9375rem',
    }),
    multiValue: base => ({
      ...base,
      alignItems: 'center',
      background: colors.blue,
      borderRadius: '0.75rem',
      display: 'flex',
      height: '1.5rem',
      justifyContent: 'center',
      margin: '2px 2px 2px 0',
      width: 'calc(50% - 2px)',
    }),
    multiValueLabel: base => ({
      ...base,
      color: '#fff',
      fontSize: '0.75rem',
    }),
    multiValueRemove: () => ({
      cursor: 'pointer',
      padding: '0 0.5rem',
      marginLeft: 'auto',
    }),
  };

  return (
    <Base className={className}>
      {label && <Label>{label}</Label>}
      <InputWrapper>
        <Select
          blurInputOnSelect
          isMulti={multi}
          isClearable={false}
          name={name}
          value={value}
          onChange={
            handleChange
              ? option => handleChange(option)
              : option => form.setFieldValue(name, option)
          }
          styles={customStyles}
          options={options}
          placeholder={placeholder}
          components={{ DropdownIndicator, MultiValueRemove }}
        />
        {toggle && <Toggle form={form} toggled={toggled} name={name} />}
      </InputWrapper>
      <ErrorMessage visible={touched && error} error={error} />
    </Base>
  );
};

export default memo(SelectInput);

const Base = styled.div`
  margin-bottom: 1.875rem;
`;

const InputWrapper = styled.div`
  align-items: center;
  position: relative;

  @media ${viewports.medium} {
    display: flex;
  }
`;
